
.nav{
    background-color:#ff0000;
}

.navTitle{
    margin-left:50px;
    vertical-align: text-bottom;
    a{
        @extend .textShadow;
        font-size:40px;
        font-weight: bolder;
        color:#ffffff;
    } 
}



.teamKlogo{
    width:auto;
    height:80px;
    // z-index: -999;
    transform: translateX(-40px);
}

@media only screen and (max-width: 600px) {
