.formContent{

  position: absolute;
  padding-top: 15px;
  width:600px;
  bottom:-70px;
  right:30px;
  background-color:rgba(222,184,135, 0.5);
  border-radius: 5px;
  border-bottom: 0.5px solid rgba(225,225,225, 0.5);
  font-family:'Noto Sans HK', sans-serif;  
  font-size:18px;
  color:black;
    .description{
     
      padding:15px 25px 15px 25px;
      
      word-wrap: break-word;
      text-align: justify;
      text-justify:inter-ideograph;
       
      span{
        font-size:1.1em;
        font-weight: bold;
      }
    }
  
  
}

.letters{

  position: absolute;
  top:120px;
  right:90px;  
  font-family: 'Noto Sans TC', sans-serif;;
  color:#fff;
  text-shadow: black 0.3em 0.2em 0.6em;
  
  p{
    font-size:40px;
    font-weight: bold;   
   
  }
  span{
    font-size:25px;
    display:inline-block;
    padding-left:5px;
  }

}

.button{
    background-color:#ff0000;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    letter-spacing: 0.5em;
    display: inline-block;
    font-size: 18px;
    font-weight: bolder;
    height:50px;
    width:130px;
    border-radius:8px;
    text-align:center;
    line-height:20px;
  
   
}

.button:hover
{
  background-color:	#ce0000;
  transform: translateY(-2px);
}

.button:active,.button:focus
{
  outline:none;
}

.footer{
  height: 500px;
  background-color:yellow;

}









